import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowCircleRight } from '@fortawesome/free-solid-svg-icons';

import { connect } from 'react-redux';
import { setCount } from '../Actions';

import './Slide.css'

function Slide(props){

    const textDiv = React.useRef();

    const {text} = props;

    const handleClick = e => {
        props.setCount(props.count + 1);
    }

    React.useEffect(() => {
        textDiv.current.scrollTop = 0;
        scrollDown();
    });

    let lastScroll;
    const scrollDown = () => {
        if(textDiv.current.scrollTop === lastScroll){
            return;
        } else {
            setTimeout(() => {
                try {
                    lastScroll = textDiv.current.scrollTop;
                    textDiv.current.scrollTop += 1;
                    scrollDown();
                } catch (error) { 
                }
            }, 100);
        }
    }

    return(
        <div className="slide-container"> 
            <aside className="slide-aside"><span>Te amaré por siempre</span></aside>
            <div className="slide-main">
                <div ref={textDiv} className="slide-text">
                    {text.map(paragraph => {
                    return(
                        <p className="slide-paragraph" key={paragraph.id}>{paragraph.text}</p>
                    )  
                    })}
                </div>
                <div onClick={handleClick} className="next-button"><FontAwesomeIcon icon={faArrowCircleRight} /></div>
            </div>     
        </div>
    )
}

const mapStateToProps = state => {
    return {
        count: state.count
    }
}

const mapDispatchToProps = {
    setCount,
}

export default connect(mapStateToProps, mapDispatchToProps)(Slide);