import React from 'react';
import { connect } from 'react-redux';

import { setCount } from '../Actions';

import './Start.css'

function Start(props){
    const handleClick = e => {
        const audio = document.getElementById('audio');
        audio.play();
        props.setCount(1)
    }

    return(
        <div className="start-container">
            <h1 className="start__title">Manuela ❤️️</h1>
            <h2 className="start__subtitle">Te amo demasiado mi niña y por eso hice esto para ti</h2>
            <button onClick={handleClick} className="start__button" type="button">Haz click aquí con tu hermosa manito</button>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        count: state.count
    }
}

const mapDispatchToProps = {
    setCount,
}

export default connect(mapStateToProps, mapDispatchToProps)(Start);