import React from 'react';
import { connect } from 'react-redux';
import Slide from '../Components/Slide';
import LastSlide from '../Components/LastSlide';

import Start from '../Components/Start';

import './Main.css';

import texto from '../text.json';

function Main({count}){
    return(
        <>
            {count === 0 ?
                <Start />
            : count === 1 ?
                <Slide text={texto.page1} />
            : count === 2 ?
                <Slide text={texto.page2} />  
            : count === 3 ?
                <Slide text={texto.page3} />
            : count === 4 ?
                <Slide text={texto.page4}/>
            : count === 5 ?
                <Slide text={texto.page5}/>
            : count === 6 &&
                <LastSlide text={texto.page6} />
            }
        </>
    )
}


const mapStateToProps = state => {
    return {
        count: state.count
    }
}

export default connect(mapStateToProps, null)(Main);