import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart } from '@fortawesome/free-solid-svg-icons';

import { connect } from 'react-redux';
import { setCount } from '../Actions';

import './Slide.css'
import './LastSlide.css'

function Slide(props){

 
    const textDiv = React.useRef();

    const {text} = props;

    const handleClick = e => {
        const audio = document.getElementById("audio");
        audio.pause();
    }

    React.useEffect(() => {
        textDiv.current.scrollTop = 0;
        scrollDown();
    });

    let lastScroll;
    const scrollDown = () => {
        if(textDiv.current.scrollTop === lastScroll){
            return;
        } else {
            setTimeout(() => {
                lastScroll = textDiv.current.scrollTop;
                textDiv.current.scrollTop += 1;
                scrollDown();
            }, 100);
        }
    }


    return(
        <div className="slide-container"> 
            <aside className="slide-aside"><span>Te amaré por siempre</span></aside>
            <div className="slide-main">
                <div ref={textDiv} className="lastSlide-text">
                    {text.map(paragraph => {
                    return(
                        <p className="slide-paragraph" key={paragraph.id}>{paragraph.text}</p>
                    )  
                    })}
                    <h1 className="teamo">Te Amo</h1>
                    <div className="heart-container">
                        <a onClick={handleClick} href='https://www.youtube.com/watch?v=mSc9BSI_RsY'><FontAwesomeIcon className="heart" icon={faHeart} /></a>
                    </div>
                </div>
            </div>     
        </div>
    )
}

const mapStateToProps = state => {
    return {
        count: state.count
    }
}

const mapDispatchToProps = {
    setCount,
}

export default connect(mapStateToProps, mapDispatchToProps)(Slide);