import React from 'react';
import {HashRouter, Route, Switch} from 'react-router-dom';

import startBackground from './Images/start.jpeg'
import background2 from './Images/background2.jpeg'
import background3 from './Images/background3.jpeg'
import background4 from './Images/background4.jpeg'
import background5 from './Images/background5.jpeg'
import background6 from './Images/background6.jpeg'
import background7 from './Images/background7.jpeg'

import Start from './Pages/Main';

import './App.css'
import { connect } from 'react-redux';

function App({count}){
    const backgroundsArray = [startBackground, background2, background3, background4, background5, background6, background7];

    console.log(count)

    const backgroundStyles = {
        height: '100vh',
        backgroundImage: `url(${backgroundsArray[count]})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        transition: 'all 1s'
    }

    return(
        <div style={backgroundStyles} className="body">
            <main className="main">
                <HashRouter>
                    <Switch>
                        <Route exact path="/" component={Start} />
                    </Switch>
                </HashRouter>
            </main>           
        </div>
    )
}

const mapStateToProps = state => {
    return{
        count: state.count
    }
}

export default connect(mapStateToProps, null)(App);